<template>
  <div class="jumbotron jumbotron-fluid">
    <div class="container">
      <h4>Est-ce que vous êtes sûr que vous voulez supprimer cet oeuvre?</h4>
      <button class="btn btn-outline-danger" type="button" v-on:click="apiDelete">OUI!</button>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'Supprimer',
  data () {
    return {
      
    }
  },
  methods: {

      apiDelete(){
        var app = this;
        axios
          .get('http://localhost/tp3/oeuvre/delete.php?id='+ this.$route.params.id)
          .then(function(response){
            
            console.log(response.data.message);
           app.$router.push('/');
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>


