<template>
  <div class="liste">
    <h1> Les oeuvres</h1>
      <router-link to="/ajouter">
        <button type="button" class="btn btn-outline-success">AJOUTER</button>
      </router-link>
      <hr>
      <div class="container">
        <div class="row">
          <div class="card" style="width: 18rem;" v-for="item in info" :key="item.id">
            <img class="card-img-top" :src="item.image" alt="Card image cap">
            <div class="card-body">
            <h5 class="card-title">{{ item.titre }}</h5>
            <h6 class="card-title">{{ item.auteur }}</h6>
            <p>{{item.id}}</p>
            <router-link class="btn btn-outline-danger" type="button" v-bind:to="{name: 'Supprimer', params: {id: item.id}}">SUPPRIMER</router-link>
            <router-link class="btn btn-outline-warning" type="button" v-bind:to="{name: 'Editer', params: {id: item.id}}">EDITER</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'Liste',
  data (){
    return {
      info: ''
    }
  },
  created: function () {
      

      axios
        .get('http://ianinabasystiuk.ca/api_vue/oeuvre/read.php')
        .then(response => (this.info = response.data.records))

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>


