<template>
  <div class="jumbotron jumbotron-fluid">
    <div class="container">
      <h1 class="display-4">{{titre}}</h1>
      <p class="lead">Fait par : {{nom}}</p>
      <p class="lead">Supervisé par : {{prof}}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Header',
  data () {
    return {
      titre: 'CRUD Vue.js 2',
      nom: 'Basystiuk Ianina',
      prof: 'Jonathan Martel'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



