<template>
<div>
    <h3>Ajoutez vos oeuvres ici :</h3>
    <form v-on:submit.prevent="apiCreate">
        <div class="form-column">
            <!-- <div class="form-group">
                <label for="id">ID</label>
                <input class="form-control" type="text" name="id" placeholder="id" v-model="id">
            </div> -->
            <div class="form-group">
                <label for="titre">Titre</label>
                <input class="form-control" type="text" name="titre" placeholder="Titre d'oeuvre" v-model="titre">
            </div>
            <div class="form-group">
                <label for="auteur">Auteur</label>
                <input class="form-control" type="text" name="auteur" placeholder="Nom d'auteur" v-model="auteur">
            </div>
            <div class="form-group">
                <label for="image">Image</label>
                <input class="form-control" type="text" name="image" placeholder="Lien vers image" v-model="image">
            </div>
            <button type="submit" class="btn btn-outline-primary">Submit</button>
            <hr>
            <h3 v-if="show1">L'oeuvre est ajouté</h3>
            <h3 v-else-if="show2">L'oeuvre n'est pas ajouté</h3>
            <button type="button" class="btn btn-outline-primary" v-on:click="lister">LISTER</button>           
        </div>
    </form>
</div>
</template>
<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'Ajouter',
  data () {
    return {
    //   id:'',
      titre:'',
      auteur:'',
      image:'',
      show1: false,
      show2: false
    }
  },
  methods:{

    apiCreate(){
        //var self = this;
        console.log({id: this.id, titre: this.titre, auteur: this. auteur, image:this.image});
        axios
            .get('http://ianinabasystiuk.ca/api_vue/oeuvre/create.php?id='+this.id+'&titre='+this.titre+'&auteur='+this.auteur+'&image='+this.image)
            .then(response => (this.show1 = true))//{
            .catch(error => (this.show2 = true))
           // self.show1 = true
            
            //console.log(response.data);
            //})

    },
    lister() {

        this.$router.push('/');
    }
  }
    

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>


